import "./style/app.scss";

import Vue from "vue";
import VueRouter from "vue-router";
import axios from "axios";

import App from "./App.vue";

Vue.use(VueRouter);
Vue.use(axios);

//router setting
const router = new VueRouter({
    mode: "hash", //tmp
    base: __dirname,
    routes: [
        {
            path: "/",
            component: App
        }
    ]
});

const app = new Vue({
    router: router,
    render: h => h(App)
}).$mount("#app");



